import React, { useState, useEffect } from 'react'
import axios from 'axios'

import './Reset.css'
import './App.css'

const App = () => {
	// USE STATE data
	const [userData, setUserData] = useState({
		meno: '',
		prezvisko: '',
		titul: '',
		ulica: '',
		mesto: '',
		psc: '',
		telefon: '',
		email: '',
		stav: '',
		vzdelanie: '',
		vek: '',
		profesia: '',
		poznamka: '',
	})
	const [data, setData] = useState({
		stav: [],
		profesie: [],
		vzdelanie: [],
		zaujemci: [],
		profesie_zaujemci: [],
	})

	// Funkcia ktora sa stara o spracovanie pisania
	const onChangeInput = (event) => {
		event.preventDefault()
		setUserData({
			...userData,
			[event.currentTarget.name]: event.currentTarget.value,
		})
	}

	// Funkcia na vytvaranie zaujemcu
	const createCustomer = () => {
		axios.post('/api/create', userData)
		    .then((response) => {
			    alert('Zaujemca bol uspesne pridany')
			    addCustomer(response.data.recordsets[0][0].ID_zaujemca, userData.profesia, userData.poznamka)
		    }, (error) => {
			console.log(error);
		    });
	}

	const addCustomer = (idZaujemca, profesia, poznamka) => {
		axios.post('/api/add', {
			ID_zaujemca: idZaujemca,
			ID_profesie: profesia,
			poznamka: poznamka
		})
		    .then((response) => {
			alert('Zaujemca bol uspesne pridany')
			getData()
		    }, (error) => {
			alert('Nastal problem pri pridavani zaujemcu')
		    });
	}

	// Funkcia na fetchovanie z databazy
	const getData = () => {
		axios
			.all([
				axios.get('/api/stav'),
				axios.get('/api/profesie'),
				axios.get('/api/vzdelanie'),
				axios.get('/api/zaujemci'),
				axios.get('/api/profzam'),
			])
			.then(
				axios.spread((stav, profesie, vzdelanie, zaujemci, profzam) => {
					if (stav.status === 200) {
						setData({
							...data,
							stav: stav.data.recordset,
							profesie: profesie.data.recordset,
							vzdelanie: vzdelanie.data.recordset,
							zaujemci: zaujemci.data.recordset,
							profesie_zaujemci: profzam.data.recordset,
						})
					}
				})
			)
			.catch((e) => alert(e))
	}

	// UseEffect that runs after
	useEffect(() => {
		getData()
	}, [])

	if ((data.stav !== [] && data.profesie !== [], data.vzdelanie !== [])) {
		return (
			<div className='app__container'>
				<div className='app__header'>
					<h1>NAJDI SI PR√ÅCU !</h1>
				</div>

				<div className='app__body'>
					<div className='body__form'>
						<h1>Formular Zujemcu</h1>
						<form
							onSubmit={(e) => {
								e.preventDefault()
								console.log(userData)
								createCustomer()
							}}
						>
								<h2>Meno</h2>
								<input
									type='text'
									name='meno'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Priezvisko</h2>
								<input
									type='text'
									name='prezvisko'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Titul</h2>
								<input 
									type='text' 
									name='titul' 
									onChange={(e) => onChangeInput(e)} 
								/>
								<h2>Ulica</h2>
								<input
									type='text'
									name='ulica'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Mesto</h2>
								<input
									type='text'
									name='mesto'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>PSC</h2>
								<input
									type='text'
									name='psc'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Telefon</h2>
								<input
									type='tel'
									name='telefon'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Email</h2>
								<input
									type='email'
									name='email'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Stav</h2>
								<select 
									name='stav' 
									required 
									onChange={(e) => onChangeInput(e)} 
									defaultValue='Vyberte'
								>
									{data.stav.map((item) => (
										<option value={item.ID_stav} key={item.ID_stav}>
											{item.stav}
										</option>
									))}
								</select>
								<h2>Vzdelanie</h2>
								<select
									name='vzdelanie'
									required
									defaultValue='Vyberte'
									onChange={(e) => onChangeInput(e)}
								>
									{data.vzdelanie.map((item) => (
										<option value={item.ID_vzdelanie} key={item.ID_vzdelanie}>
											{item.dosiahnute_vzdelanie}
										</option>
									))}
								</select>
								<h2>Vek</h2>
								<input
									type='number'
									name='vek'
									required
									onChange={(e) => onChangeInput(e)}
								/>
								<h2>Profesia</h2>
								<select
									name='profesia'
									required
									defaultValue='Vyberte'
									onChange={(e) => onChangeInput(e)}
								>
									{data.profesie.map((item) => (
										<option value={item.ID_profesie} key={item.ID_profesie}>
											{item.nazov_profesie}
										</option>
									))}
								</select>
								<h2>Poznamka</h2>
								<textarea
									name='poznamka'
									rows='10'
									onChange={(e) => onChangeInput(e)}
								></textarea>
							<button type='submit'>ZAREGISTRUJ</button>
						</form>
					</div>

					<div className='body__table'>
						<h1>TABULKA</h1>
						{
						data.profesie_zaujemci !== [] ? 
							<table>
								<tr>
									<th>Titul</th>
									<th>Meno</th>
									<th>Priezvisko</th>
									<th>Stav</th>
									<th>Nazov profesie</th>
									<th>poznamka</th>
								</tr>
								{data.profesie_zaujemci.map((item)=>{
									return (
										<tr>
											<th>{item.titul}</th>
											<th>{item.meno}</th>
											<th>{item.priezvisko}</th>
											<th>{item.stav}</th>
											<th>{item.nazov_profesie}</th>
											<th>{item.poznamka}</th>
										</tr>
									)
								})}
								
							</table> 
						:
							<div>NEPODARILO SA SPOJIT S DBBBBB</div>
						}
					</div>
				</div>

				<div className='app__footer'>
					<h1>&copy;Pekny den&copy;</h1>
				</div>
			</div>
		)
	} else {
		return (
			<div>NEPODARILO SA PRIPOJIT K DB</div>
		)
	}
}

export default App
